.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background: var(--CARD-BG);
  color: var(--TEXT-COLOUR2);
  flex: 1;
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  position: relative;
  overflow: hidden;
  transition: background 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--CARD-HOVER);
}
.card--facebook {
  border-top: 0.3125rem solid var(--FACEBOOK-COLOUR);
}
.card--twitter {
  border-top: 0.3125rem solid var(--TWITTER-COLOUR);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  content: "";
  width: 100%;
  height: 0.3125rem;
  width: 100%;
  background: linear-gradient(225deg, var(--INSTAGRAM-END-COLOUR), var(--INSTAGRAM-START-COLOUR) 100%);
}
.card--youtube {
  border-top: 0.3125rem solid var(--YOUTUBE-COLOUR);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 1.25rem;
  margin-bottom: 1.75rem;
  margin-top: 0.3125rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--TEXT-COLOUR);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  color: var(--TEXT-COLOUR2);
  font-size: 0.75rem;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
  margin-bottom: 0.5625rem;
}
.card__change {
  font-size: 0.75rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card__change--up {
  color: var(--LIMEGREEN);
}
.card__change--down {
  color: var(--BRIGHTRED);
}
.card__change img {
  margin-right: 0.25rem;
}